<template>
  <div id="selfHelp" v-loading="loading" element-loading-background="#f8f8f8">
    <el-row>
      <el-row>
        <h2>个人信息修改</h2>
        <el-divider></el-divider>
      </el-row>
      <el-row style="margin-bottom: 20px">
        <el-col>
          <el-upload
            action="#"
            :show-file-list="false"
            :auto-upload="false"
            :multiple="false"
            :on-change="uploadFile"
            drag
            accept="image/jpg,image/jpeg,image/png"
          >
            <div>
              <i
                class="el-icon-picture"
                style="margin-top: 40px; font-size: 40px; color: #999a9c"
              ></i>
              <div>上传图片</div>
              <div>格式为png、jpeg或jpg</div>
            </div>
          </el-upload>
        </el-col>
      </el-row>
      <el-divider v-if="imageUrl"></el-divider>
      <el-row>
        <el-col>
          <i
            v-if="imageUrl"
            class="el-icon-circle-close deleteImg"
            @click.stop="handleRemove"
          ></i>
          <el-image
            v-if="imageUrl"
            :src="imageUrl"
            class="el-upload--picture-car"
            :preview-src-list="[imageUrl]"
            style="width: 100px; height: 100px; margin: 10px 0"
          />
        </el-col>
      </el-row>
    </el-row>
    <el-divider v-if="imageUrl"></el-divider>
    <el-row>
      <el-form
        :model="temp"
        :rules="rules"
        ref="ruleForm"
        label-width="50px"
        class="demo-ruleForm"
      >
        <el-form-item label="账号" prop="account">
          <el-input
            size="mini"
            v-model="temp.account"
            clearable
            class="el-input-plus"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input
            size="mini"
            v-model="temp.password"
            clearable
            class="el-input-plus"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="电话" prop="phone">
          <el-input
            size="mini"
            v-model="temp.phone"
            clearable
            class="el-input-plus"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input
            size="mini"
            v-model="temp.email"
            clearable
            class="el-input-plus"
          >
          </el-input>
        </el-form-item>
        <el-form-item> </el-form-item>
      </el-form>
    </el-row>
    <el-row>
      <el-popconfirm
        title="确定要修改以下个人信息？"
        confirm-button-text="确认"
        cancel-button-text="取消"
        @confirm="update"
      >
        <el-button
          slot="reference"
          class="el-icon-edit"
          size="mini"
          type="primary"
          >修改</el-button
        >
      </el-popconfirm>
      <el-divider></el-divider>
    </el-row>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "YSelfHelp",
  data() {
    return {
      loading: true,
      temp: {
        account: "",
        password: "",
        phone: "",
        email: "",
        image: "",
      },
      rules: {
        account: [
          {
            pattern: /^[A-Za-z0-9\u4e00-\u9fa5]{6,10}$/,
            message: "6到10位的数字、大小写字母、中文",
            trigger: "blur",
          },
        ],
        password: [
          {
            pattern: /^[A-Za-z0-9]{6,12}$/,
            message: "6到12位的数字、大小写字母",
            trigger: "blur",
          },
        ],
        phone: [
          {
            pattern:
              /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,
            message: "电话号码格式不符",
            trigger: "blur",
          },
        ],
        email: [
          {
            type: "email",
            message: "电子邮箱格式不符",
            trigger: "blur",
          },
        ],
      },
      imageUrl: "",
    };
  },
  methods: {
    ...mapActions("user", { updateUserInfo: "updateUserInfo" }),
    uploadFile(item) {
      this.temp.image = item.raw; // 图片文件
      if (window.createObjectURL != undefined) {
        // 图片上传浏览器回显地址
        // basic
        this.imageUrl = window.createObjectURL(item.raw);
      } else if (window.URL != undefined) {
        // mozilla(firefox)
        this.imageUrl = window.URL.createObjectURL(item.raw);
      } else if (window.webkitURL != undefined) {
        // webkit or chrome
        this.imageUrl = window.webkitURL.createObjectURL(item.raw);
      }
      console.log(this.imageUrl, "imageUrl");
    },
    handleRemove() {
      this.imageUrl = "";
    },
    update() {
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          let formData = new FormData();
          formData.append("usrId", this.$store.state.user.userForm.usrId);
          formData.append("usrAccount", this.temp.account);
          formData.append("usrPwd", this.temp.password);
          formData.append("usrEmail", this.temp.email);
          formData.append("usrPhone", this.temp.phone);
          formData.append("avatar", this.temp.image);
          const data = {
            _this: this,
            data: formData,
          };
          this.updateUserInfo(data);
        } else {
          this.$message({
            message: "请检查输入项是否符合要求",
            type: "warning",
          });
          return false;
        }
      });
    },
  },
  activated() {
    console.log("activated");
    setTimeout(() => {
      this.loading = false;
    }, 1000);
  },
  deactivated() {
    console.log("deactivated");
    this.loading = true;
  },
};
</script>

<style scoped>
#selfHelp {
  padding: 30px 25px;
}
p {
  margin: 5px 0;
  font-size: 12px;
}
.el-input-plus {
  margin: 2px 0;
  max-width: 300px;
}
.deleteImg {
  font-size: 30px;
  position: absolute;
  top: 0;
  right: 0px;
  z-index: 999;
  transition: all 0.3s;
}
.deleteImg:hover {
  color: #7e7b7b;
}
img {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
}
</style>
